# Config [[dev]]

See [UWHealth sass tools documenation](https://uwhealth.github.io/sass-tools/) for configuration variable details.
What is set here are **reasonable defaults**.

*/
.demo-navigation {
  display: flex;
  position: relative;
  z-index: 2;
  box-shadow: 0 4px 6px rgba(10, 28, 43, 0.2);
  background-color: #fff;
  width: 100%;
}

.demo-navigation__link {
  flex: 1 1 auto;
  margin-left: 0.375rem;
  padding: 1.125rem 0.75rem;
  max-width: 7.5rem;
  text-align: center;
}

.demo-navigation__link:hover {
  background-color: #d3e2ed;
  color: #4c95bd;
}

.demo-header {
  display: flex;
  align-items: center;
  background-color: #e4eaf3;
  background-image: linear-gradient(rgba(211, 226, 237, 0) 0%, #d3e2ed 100%);
  width: 100%;
  height: 70vh;
  max-height: 37.5rem;
}

.demo-header__title {
  padding-bottom: 1.5rem;
  letter-spacing: .01em;
  color: rgba(8, 44, 74, 0.6);
  font-weight: normal;
}

.demo-body {
  padding-top: 1.5rem;
}

.demo-body__head,
.demo-category {
  font-weight: normal;
}

.demo-body__head {
  font-size: 1.26562rem;
  line-height: 1.18519;
  margin-bottom: 1.875rem;
  border-bottom: 2px solid rgba(146, 182, 202, 0.7);
  padding-bottom: calc(0.75rem - 1px);
  color: #92b6ca;
}

.demo-category {
  font-size: 1.125rem;
  line-height: 1.33333;
  padding-top: 2.25rem;
}
